.dimmer {
  position: relative;
  width: 100vh;
  height: 100vh;
}

.themodal {
  position: absolute;
  top: auto;
  left: auto;
  height: auto;
  max-height: 70vh;
}

.ui.modal > .close {
  top: 1.0535rem;
  right: 1rem;
  color: rgba(0, 0, 0, 0.87);
}

.ui.fullscreen {
  margin-left: 2rem;
}
